.Carousel{
  width: 89%;
  margin: auto;
  background-color: #DCE5E2;
  margin-top: 30px;
  border-radius: 20px;
  transition: 0.4s;

  .top-section{
    display: flex;
    align-items: center;
    img{
      width: 1%;
      margin: 0 40px 0 40px;
    }
    .left-section{
      width: 50%;
      h1{
        font-family: ReadexPro-Regular;
        font-size: 55px;
        font-weight: 400;
        width: 90%;
        margin: 70px 0 0 0;
      }
      p{
        width: 90%;
        font-size: 15px;
        font-family: Inter-Medium;
        margin-top: 20px;
      }
      button{
        border: 1px solid black;
        outline: none;
        background-color: rgba(220, 229, 226, 0);
        width: 23%;
        height: 46px;
        border-radius: 100px;
        margin-top: 30px;
        font-family: Inter-Medium;
        font-size: 16px;
        padding: 10px 0 10px 0;
        transition: 0.2s;
        &:hover{
          background-color: black;
          color: #DCE5E2;
        }
      }
    }
    .right-section{
      width: 50%;
      height: 550px;
      display: flex;
      align-items: center;
      padding-top: 10px;
      img{
        width: 110%;
        margin: 0 50px 0 0;
      }
    }
  }
  .bottom-section{
    display: flex;
    width: 50%;
    margin: auto;
    justify-content: space-between;
    padding-bottom: 30px;
    margin-top: 10px;
    img{
      width: 28%;
      z-index: 3;
    }
  }
}