.support{
  width: 90%;
  margin: auto;
  margin: 50px;
  text-align: center;
  h2{
    font-size: 30px;
    font-family: ReadexPro-Regular;
  }
  form{
    display: flex;
    flex-direction: column;
    align-items: center;
    input{
      height: 30px;
      &:focus{
        height: 40px;
      }
    }
    .input{
      margin-top: 20px;
      width: 25%;
      border: 0;
      outline: 0;
      box-shadow: 1px 1px 10px #dedede;
      border-radius: 7px;
      font-family: ReadexPro-Regular;
      font-size: 16px;
      padding: 10px;
      transition: 0.3s;
      &:focus{
        box-shadow: 1px 1px 10px #b7b7b7;
      }
    }
    textarea{
      height: auto;
    }
    #submit{
      margin-top: 20px;
      width: 26.3%;
      border: 0;
      outline: 0;
      color: black;
      border: 2px solid black;
      background-color: white;
      font-family: ReadexPro-Regular;
      font-size: 16px;
      transition: 0.3s;
      height: 45px;
      &:hover{
        width: 30%;
        background-color: black;
        color: white;
        border-radius: 7px;
      }
    }
  }
}