header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 90%;
  margin: auto;
  padding: 20px 0 20px 0;
  h1{
    font-size: 35px;
    margin: 0;
    width: 10%;
  }
  a{
    text-decoration: none;
    color: black;
  }
  nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 20%;
    margin-left: 50px;
    p{
      font-size: 15px;
      font-family: Inter-Medium;
    }
    a{
      text-decoration: none;
      color: black;
      font-size: 15px;
      font-family: Inter-Medium;
    }
  }
  div{
    display: flex;
    justify-content: space-between;
    width: 6%;
    img{
      cursor: pointer;
    }
  }
}