.LatestOffers{
  text-align: center;
  width: 90%;
  margin: auto;
  margin-top: 80px;
  h2{
    font-family: ReadexPro-Regular;
    font-size: 40px;
    font-weight: 400;
    margin: 0;
  }
  p{
    font-family: ReadexPro-Regular;
    font-size: 14px;
    width: 42%;
    margin: auto;
    margin-top: 10px;
  }
  .collections{
    margin-top: 50px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding-bottom: 100px;
    .block{
      width: 24%;
      box-shadow: 5px 5px 30px #ababab;
    }
    .first-block{
      width: 48%;
      text-align: end;
      .first-top-block{
        display: flex;
        flex-direction: column;
        align-items: end;
        box-shadow: 5px 5px 30px #ababab;
        img{
          width: 75%;
          margin: auto;
        }
        span{
          margin-right: 50px;
          font-family: Inter-Medium;
        }
        h3{
          font-family: ReadexPro-Regular;
          margin: 0;
          margin-right: 50px;
          font-size: 30px;
          width: 30%;
          font-weight: 400;
          margin-bottom: 10px;
        }
      }
      .first-bottom-block{
        box-shadow: 5px 5px 25px #ababab;
        display: flex;
        align-items: center;
        margin-top: 80px;
        div{
          display: flex;
          flex-direction: column;
          align-items: end;
          margin-right: 50px;
          span{
            font-family: Inter-Medium;
          }
          h3{
            font-family: ReadexPro-Regular;
            margin: 0;
            font-size: 30px;
            width: 100%;
            font-weight: 400;
            margin-bottom: 10px;
          }
        }
        img{
          width: 60%;
        }
      }
    }
    .second-block{
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      img{
        width: 100%;
      }
      span{
        font-family: Inter-Medium;
        margin-top: 40px;
      }
      h3{
        font-family: ReadexPro-Regular;
        margin: 0;
        font-size: 30px;
        width: 80%;
        font-weight: 400;
        margin-bottom: 10px;
      }
    }
    .third-block{
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      img{
        width: 100%;
      }
      span{
        font-family: Inter-Medium;
      }
      h3{
        font-family: ReadexPro-Regular;
        margin: 0;
        font-size: 30px;
        width: 80%;
        font-weight: 400;
        margin-bottom: 40px;
      }
    }
  }
}