.Cards {
  width: 90%;
  margin: auto;

  .set {
    display: flex;
    width: 100%;
    margin: auto;
    margin-top: 40px;
    margin-bottom: 50px;
    justify-content: space-between;
    flex-wrap: wrap;
    text-decoration: none;
    transition: 0.3s;

    .object {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 24%;
      padding: 25px;
      text-align: start;
      transition: 0.3s;
      box-sizing: border-box;
      border-radius: 20px;
      background-color: #ffffff;
      cursor: pointer;
      text-decoration: none;
      margin-top: 30px;
      height: 450px;
      img {
        width: 55%;
        margin: auto;
      }

      .signature {
        display: flex;
        flex-direction: column;
        align-items: start;

        h3 {
          font-size: 22px;
          font-family: ReadexPro-Regular;
          font-weight: 400;
          margin: 35px 0 0 0;
        }

        p {
          margin: 0;
          font-family: Inter-Medium;
          width: 100%;
          margin-top: 15px;
          color: rgba(75, 75, 75, 1);
          font-size: 14px;
          text-decoration: none;
        }

        span {
          margin-top: 35px;
          font-family: Inter-Medium;
          font-size: 18px;
        }
      }

      &:hover {
        box-shadow: 2.5px 2.5px 10px #ababab;
      }
    }

  }
}