.Card{
  display: flex;
  justify-content: space-between;
  width: 90%;
  margin: auto;
  margin-top: 70px;
  .left-section-product{
    display: flex;
    align-items: center;
    width: 50%;
    img{
      width: 60%;
    }
  }
  .right-section-product{
    width: 50%;
    margin-top: 30px;
    h1{
      font-family: Inter-Medium;
      font-size: 40px;
      margin: 0;
      margin-bottom: 20px;
    }
    p{
      font-family: ReadexPro-Regular;
      margin-top: 40px;
      line-height: 26px;
      font-size: 14px;
    }
    span{
      font-family: ReadexPro-Regular;
      font-size: 14px;
    }
    .price{
      margin-top: 50px;
      width: 100%;
      p {
        font-size: 28px;
        margin-top: 10px;
        margin-bottom: 10px;
      }
      button{
        border: 1px solid black;
        outline: none;
        background-color: rgba(220, 229, 226, 0);
        width: 23%;
        height: 42px;
        border-radius: 100px;
        font-family: Inter-Medium;
        font-size: 16px;
        margin-top: 50px;
        padding: 10px 0 10px 0;
        transition: 0.2s;
        &:hover{
          background-color: black;
          color: #ffffff;
        }
      }
    }
  }
}