.basket{
  width: 90%;
  margin: auto;
  margin-top: 50px;
  margin-bottom: 70px;
  .basket-block{
    padding: 20px 40px 20px 30px;
    border-radius: 10px;
    box-shadow: 2.5px 2.5px 10px #e5e5e5;
    display: flex;
    justify-content: space-between;
    transition: 0.25s;
    cursor: pointer;
    margin-top: 30px;
    min-height: 150px;
    .basket-block-img{
      width: 10%;
      img{
        width: 100%;
      }
    }
    div{
      width: 82%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      h2{
        font-family: Inter-Medium;
        font-size: 25px;
      }
      span{
        font-family: Inter-Medium;
        font-size: 18px;
      }
    }
    &:hover{
      box-shadow: 1px 1px 10px #b4b4b4;
    }
  }
  .summ{
    text-align: right;
    width: 95%;
    margin: auto;
    margin-top: 50px;
    font-family: Inter-Regular;
    font-weight: 600;
    font-size: 25px;
  }
}