.Items {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;

  h2 {
    font-family: ReadexPro-Regular;
    font-size: 40px;
    font-weight: 400;
    margin: 0;
  }

  p {
    font-family: ReadexPro-Regular;
    font-size: 14px;
    width: 42%;
    margin: auto;
    margin-top: 10px;
  }

  .set {
    display: flex;
    width: 90%;
    margin-top: 50px;
    margin-bottom: 50px;
    justify-content: space-between;

    text-decoration: none;

    .object {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 24%;
      padding: 25px;
      text-align: start;
      transition: 0.4s;
      box-sizing: border-box;
      border: 2px solid rgba(0, 0, 0, 0);
      border-radius: 30px;
      cursor: pointer;
      text-decoration: none;

      img {
        width: 80%;
        margin: auto;
      }

      .signature {
        display: flex;
        flex-direction: column;
        align-items: start;
        transition: 0.3s;
        h3 {
          font-size: 22px;
          font-family: ReadexPro-Regular;
          font-weight: 400;
          margin: 35px 0 0 0;
        }

        p {
          margin: 0;
          font-family: Inter-Medium;
          width: 100%;
          margin-top: 15px;
          color: rgba(75, 75, 75, 1);
          font-size: 14px;
          text-decoration: none;
        }

        span {
          margin-top: 35px;
          font-family: Inter-Medium;
          font-size: 18px;
        }
      }

      &:hover {
        box-shadow: 2.5px 2.5px 10px #ababab;
      }
    }

  }
}